.fade-in-linear-enter-active,
.fade-in-linear-leave-active {
  transition: opacity 200ms linear; }

.fade-in-linear-enter,
.fade-in-linear-leave,
.fade-in-linear-leave-active {
  opacity: 0; }

/** tu-fade-in-linear */
.tu-fade-in-easeout-enter-active,
.tu-fade-in-easeout-leave-active {
  transition: opacity 0.3s ease-out; }

.tu-fade-in-easeout-enter,
.tu-fade-in-easeout-leave,
.tu-fade-in-easeout-leave-active {
  opacity: 0; }

/** tu-fade-in */
.tu-fade-in-enter-active,
.tu-fade-in-leave-active {
  transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-fade-in-enter,
.tu-fade-in-leave-active {
  opacity: 0; }

/** tu-popper-in-center */
.tu-popper-in-center-enter-active,
.tu-popper-in-center-leave-active {
  transition: all 0.2s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-popper-in-center-enter,
.tu-popper-in-center-leave-active {
  scale: 0;
  transform: scaleX(0); }

/** tu-zoom-in-center */
.tu-zoom-in-center-enter-active,
.tu-zoom-in-center-leave-active {
  transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-zoom-in-center-enter,
.tu-zoom-in-center-leave-active {
  opacity: 0;
  transform: scaleX(0); }

/** tu-zoom-in-top */
.tu-zoom-in-top-enter-active,
.tu-zoom-in-top-leave-active {
  opacity: 1;
  transform: scaleY(1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: center top; }

.tu-zoom-in-top-enter,
.tu-zoom-in-top-leave-active {
  opacity: 0;
  transform: scaleY(0); }

.tu-zoom-in-bottom-enter-active,
.tu-zoom-in-bottom-leave-active {
  opacity: 1;
  transform: scaleY(1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: center bottom; }

.tu-zoom-in-bottom-enter,
.tu-zoom-in-bottom-leave-active {
  opacity: 0;
  transform: scaleY(0); }

/** tu-zoom-in-left */
.tu-zoom-in-left-enter-active,
.tu-zoom-in-left-leave-active {
  opacity: 1;
  transform: scale(1, 1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: top left; }

.tu-zoom-in-left-enter,
.tu-zoom-in-left-leave-active {
  opacity: 0;
  transform: scale(0.45, 0.45); }

.collapse-transition {
  transition: 0.3s height ease-in-out, 0.3s padding-top ease-in-out, 0.3s padding-bottom ease-in-out; }

.horizontal-collapse-transition {
  transition: 0.3s width ease-in-out, 0.3s padding-left ease-in-out, 0.3s padding-right ease-in-out; }

.tu-list-enter-active,
.tu-list-leave-active {
  transition: all 0.48s; }

.tu-list-enter,
.tu-list-leave-active {
  opacity: 0;
  transform: translateX(-40px); }

.tu-opacity-transition {
  transition: opacity 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

html,
body {
  height: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

body {
  -webkit-font-smoothing: antialiased;
  font-size: 14px;
  color: #9baacf;
  -webkit-font-smoothing: antialiased; }

/* BEM [block]__[element]--[modifier]
 -------------------------- */
.tu-cascader {
  display: inline-block;
  position: relative;
  font-size: 14px;
  line-height: 32px;
  width: 100%; }
  .tu-cascader:not(.is-disabled):hover .tu-input__inner {
    cursor: pointer; }
  .tu-cascader .tu-input {
    cursor: pointer; }
    .tu-cascader .tu-input .tu-input__inner {
      text-overflow: ellipsis; }
    .tu-cascader .tu-input .tu-icon-down {
      transition: transform 0.3s;
      font-size: 14px; }
      .tu-cascader .tu-input .tu-icon-down.is-reverse {
        transform: rotateZ(180deg); }
    .tu-cascader .tu-input .tu-icon-close-circle-fill:hover {
      color: #9baacf; }
    .tu-cascader .tu-input.is-focus .tu-input__inner {
      box-shadow: 3px 3px 6px #c8d0e7, -2px -2px 5px #ffffff; }
  .tu-cascader.is-disabled .tu-cascader__label {
    z-index: 2;
    color: #bec8e4; }
  .tu-cascader__dropdown {
    margin: 5px 0;
    box-shadow: 3px 3px 6px #c8d0e7, -2px -2px 5px #ffffff; }
  .tu-cascader__tags {
    position: absolute;
    left: 0;
    right: 30px;
    top: 50%;
    transform: translateY(-50%);
    display: flex;
    flex-wrap: wrap;
    line-height: normal;
    text-align: left;
    box-sizing: border-box; }
    .tu-cascader__tags .tu-tag {
      margin: 4px 0 4px 6px;
      text-overflow: ellipsis; }
  .tu-cascader__suggestion-panel {
    border-radius: 4px; }
  .tu-cascader__suggestion-list {
    max-height: 204px;
    margin: 0;
    padding: 6px 0;
    font-size: 14px;
    color: #9baacf;
    text-align: center; }
  .tu-cascader__suggestion-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 36px;
    padding: 0 15px;
    text-align: left;
    outline: none;
    cursor: pointer;
    transition: background-color 0.3s; }
    .tu-cascader__suggestion-item:hover, .tu-cascader__suggestion-item:focus {
      background-color: rgba(200, 208, 231, 0.38); }
    .tu-cascader__suggestion-item.is-checked {
      color: #6d5dfc;
      font-weight: bold; }
    .tu-cascader__suggestion-item > span {
      margin-right: 10px; }
  .tu-cascader__empty-text {
    margin: 10px 0;
    color: #bec8e4; }
  .tu-cascader__search-input {
    flex: 1;
    height: 24px;
    min-width: 60px;
    margin: 1px 0 1px 12px;
    padding: 0;
    border: none;
    outline: none;
    box-sizing: border-box;
    background-color: transparent;
    color: #9baacf;
    font-size: 14px; }
    .tu-cascader__search-input::placeholder {
      color: #bec8e4; }
